import React, {useState} from "react";

const Form2 = () => {

    const [name, setName] = useState('')

    function updateName(event) {
        setName(event.target.value)
    }

    function capitalise() {
        setName(name[0].toLocaleUpperCase() + name.substring(1))
    }

    return (
        <div>
            <input type="text" placeholder="type something" onChange={updateName} />
            <button onClick={capitalise}>Capitalise Name</button>
            <p>{name}</p>
        </div>
    )

}

export default Form2;